.backdropp {
    width: 100vw;
    height: 100vh;
    position:fixed;
    top: 0;
    transition: opacity 500ms, visibility 500ms;
    &.hidden .modal {
      transform: translate(-50%, -50%) scale(0.1);
    }
  }

.modal {
    max-width: 250px;
    width: 100%;
    // min-height: 609px;
    border-radius: 4px;
    position: absolute;
    background-color: var(--mob-menu-back-color);
    top: 50%;  
    left: 50%;
    transform: translate(-50%, -50%)  scale(1);
    transition: transform 500ms;
    padding: 30px;
    @media  screen and (min-width: 768px) {
        max-width: 320px;
      }
    @media  screen and (min-width: 1200px) {
      max-width: 528px;
    //   min-height: 502px;
    }
}

.modal-close {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    position:absolute;
    border: none;
    background-color: transparent;
    top: 8px;
    right: 8px;
    cursor: pointer;
    outline: none;
    transition: fill var(--transition-time)   var(--transition-function);
    &:hover, &:focus {
      fill: white;
    }
  }

  .modal-title {
    @include font(16px, normal, 21px, 0.04em);
    font-family: Titan One;
    font-style: normal;
    text-transform: uppercase;
    text-align: center;
    color: var(--clear-white);
  }

  .input-txt {
    @include font(10px, normal, 12px, 0.04em);
    font-family: DM Sans;
    font-style: normal;
    text-transform: uppercase;
    text-align: center;
    color: var(--clear-white);
  }

  .form-input {
      width: 100%;
      height: 30px;
      border: 1px solid rgba(33, 33, 33, 0.2);
      border-radius: 15px;
      padding-left: 40px;
      cursor: pointer;
      outline: none;
      transition: border-color var(--transition-time)   var(--transition-function) ;
      &:hover, &:focus {
        border-color: var(--light-brown-background);
        color: var(--light-brown-background);
        }
        &::placeholder {
        font-size: 14px;
        line-height: 1.14;
        letter-spacing: 0.01em;
        color: rgba(117, 117, 117, 0.5);
        }
        .products-list {
            // padding: 0;
            // margin: 0;
            // list-style: none;
            display: flex;
            flex-wrap: wrap;
            margin-left: -10px;
            margin-top: -10px;
        }

        .products-item {
            margin-left: 10px;
            margin-top: 10px;
            flex-basis: calc(100%/2 - 10px);
        }
  }